import {SafeAreaView, useSafeAreaInsets} from 'react-native-safe-area-context';
import HeaderTicketDetail from '../components/Header';
import TextCM from '../../../components/Text';
import {useCallback, useEffect, useMemo, useRef, useState} from 'react';
import {IComments} from './interface';
import {useFocusEffect, useRoute} from '@react-navigation/native';
import {ITicket} from '../../../../model/interface/service.interface';
import TextFieldCM from '../../../components/TextField';
import {SizeDP} from '../../../../constants/Size';
import {IconGetRelative, IconSend} from '../../../../assets/images';
import {
  FlatList,
  Keyboard,
  KeyboardAvoidingView,
  Platform,
  ScrollView,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import CommentItem from './component/CommentItem';
import {useAppSelector} from '../../../../hooks/useRedux';
import {
  decodeUnicodeToEmoji,
  encodeEmojiToUnicode,
  filterCommentsByParentId,
} from './helper';
import ImageCropPicker from '../../../components/ImageCropPicker';
import {useTicketServices} from '../../../../hooks/apiHooks/useTicketServices';

const CommunicationTicketScreen = () => {
  const route = useRoute();
  const insets = useSafeAreaInsets();
  const {accessToken} = useAppSelector(state => state.common);
  const [listComments, setListComments] = useState<IComments[]>([]);
  const [listEmail, setListEmail] = useState<string[]>([]);
  const [listUsers, setListUsers] = useState<any[]>([]);
  const [comment, setComment] = useState<string>('');
  const [fileSelect, setFileSelect] = useState<any>();
  const [targetComment, setTargetComment] = useState<IComments | null>(null);

  const [actionType, setActionType] = useState<'new' | 'reply' | 'edit'>('new');
  const [heightOfKeyboard, setHeightOfKeyboard] = useState(0);
  const inputRef = useRef<TextInput>(null);

  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {dataAccount} = useAppSelector(state => state.account);
  const ListServices = useTicketServices();
  useEffect(() => {
    const sub = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidShow' : 'keyboardWillShow',
      e => {
        setHeightOfKeyboard(e.endCoordinates.height);
      },
    );
    const sub1 = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidHide' : 'keyboardWillHide',
      e => setHeightOfKeyboard(0),
    );

    return () => {
      sub.remove();
      sub1.remove();
    };
  }, []);

  useFocusEffect(
    useCallback(() => {
      getListComments();
    }, []),
  );

  const getListComments = async () => {
    try {
      const params = {
        userTaskId: 'all',
        ticketId: dataTicketRedux.id,
        sort: 'lastModifiedDate,desc',
        page: 0,
        size: 100000,
      };
      const data = await ListServices.getListComments(params);
      if (data && data.content) {
        setListComments(data.content);

        const filterEmail = data.content.map(
          (item: IComments) => item.createdBy,
        ) as string[];
        const emailUser = dataAccount.email || '';
        setListEmail([...new Set([...filterEmail, emailUser])]);
        getListUsers([...new Set([...filterEmail, emailUser])]);
      }
    } catch (error) {}
  };

  const commentMap = useMemo(() => {
    const map = new Map<any, IComments[]>();
    listComments.forEach(comment => {
      const parentId = comment.commentId ?? 'root';
      const siblings = map.get(parentId) || [];
      map.set(parentId, [...siblings, comment]);
    });
    return map;
  }, [listComments]);

  const sendComment = async (commentParentId: any) => {
    try {
      const listUserTask = [
        {
          id: 5845,
          email: 'nepnhi27@gmail.com',
          custId: 1833,
          orgIn: '/1833/5390/5392',
        },
      ];
      const commentPayload = {
        content: encodeEmojiToUnicode(comment),
        userTaskId: 'all',
        ticketId: dataTicketRedux.id,
        name: `${dataAccount.firstName} ${dataAccount.lastName}`,
        createdBy: dataAccount.createdBy,
        commentType: 'comment',
        avatar: dataAccount.imageUrl,
        commentId: commentParentId,
        userId: dataAccount.id,
        orgIn: dataAccount.orgIn,
        custId: dataAccount.custId,
        contactDTO: [],
        isPrivate: false,
      };
      const formData = new FormData();
      formData.append('uploadFile', null);
      formData.append('commentJson', JSON.stringify(commentPayload));
      const data = await ListServices.sendComment(formData);
      if (data) {
        setListComments([data, ...listComments]);
        Keyboard.dismiss();
      }
    } catch (error) {}
  };

  const handleReply = useCallback(async () => {
    if (actionType === 'reply' || actionType === 'new') {
      if (comment.trim()) {
        const replyingTo =
          targetComment && targetComment?.commentId
            ? targetComment?.commentId
            : targetComment?.id;
        await sendComment(replyingTo);
      }
    } else if (actionType === 'edit') {
      if (comment.trim()) {
        if (targetComment) {
          await updatedComment(targetComment?.id, targetComment?.commentId);
        }
      }
    }
    setComment('');
    setActionType('new');
    setTargetComment(null);
  }, [comment, actionType, targetComment, fileSelect]);

  const deleteComment = async (commentId: string) => {
    try {
      const data = await ListServices.deleteComment(commentId);
      if (data === 204) {
        handleDelete(commentId);
      }
    } catch (error) {}
  };

  const handleDelete = useCallback((commentId: any) => {
    setListComments(prevComments => {
      return prevComments.filter(comment => comment.id !== commentId);
    });
  }, []);

  const updatedComment = async (id: string, commentId: any) => {
    try {
      const commentPayload = {
        id: id,
        content: encodeEmojiToUnicode(comment),
        userTaskId: 'all',
        ticketId: dataTicketRedux.id,
        name: `${dataAccount.firstName} ${dataAccount.lastName}`,
        createdBy: dataAccount.createdBy,
        commentType: 'comment',
        avatar: dataAccount.imageUrl,
        commentId: commentId,
        userId: dataAccount.id,
        orgIn: dataAccount.orgIn,
        custId: dataAccount.custId,
        contactDTO: [],
        isPrivate: false,
      };
      const formData = new FormData();
      formData.append('uploadFile', null);
      formData.append('commentJson', JSON.stringify(commentPayload));
      const data = await ListServices.updateComment(formData);
      console.log('data', data);

      if (data) {
        setListComments(prevComments => {
          const newData = prevComments.map(commentData =>
            commentData.id === id
              ? {
                  ...commentData,
                  content: comment,
                }
              : commentData,
          );
          return newData;
        });
        Keyboard.dismiss();
      }
    } catch (error) {}
  };

  const getListUsers = async (listAccount: string[]) => {
    try {
      const data = await ListServices.getMultiLoginAccount(
        listAccount,
        accessToken,
      );
      if (data) {
        console.log(data);

        setListUsers(data);
      }
    } catch (error) {}
  };

  const handleChangeText = (text: string) => {
    if (text.length === 1 && text.includes('@')) {
      console.log('first');
    }
    if (text.includes(' @')) {
      console.log(1111);
      setComment(text.replace(' @', ' ' + listEmail[0]));
    } else {
      setComment(text);
    }
  };

  const handleReplyClick = useCallback(
    (action: 'reply' | 'edit', commentEdit: IComments) => {
      setActionType(action);
      if (action === 'edit') {
        handleChangeText(decodeUnicodeToEmoji(commentEdit.content));
      } else {
        handleChangeText('');
      }
      setTargetComment(commentEdit);
      inputRef.current?.focus();
    },
    [],
  );

  const choosePhoto = async () => {
    try {
      const data = await ImageCropPicker.ChoosePhoto();
      console.log(data);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <View style={{flex: 1}}>
        <FlatList
          style={{flex: 1}}
          showsVerticalScrollIndicator={false}
          contentContainerStyle={{rowGap: SizeDP(16)}}
          data={commentMap?.get('root') || []}
          renderItem={({item}) => (
            <CommentItem
              data={item}
              user={listUsers.find(user => user.email === item.createdBy)}
              setComment={setComment}
              deleteComment={deleteComment}
              updateComment={updatedComment}
              allComments={commentMap}
              onReplyClick={handleReplyClick}
              level={0}
            />
          )}
        />
      </View>
      <View
        style={[
          styles.ctnSend,
          {
            bottom:
              heightOfKeyboard > 0
                ? heightOfKeyboard -
                  Number(`${Platform.OS === 'android' ? 70 : 50}`)
                : 0,
            zIndex: 1,
            backgroundColor: '#fff',
            paddingVertical: 10,
          },
        ]}>
        <TouchableOpacity onPress={choosePhoto} hitSlop={SizeDP(15)}>
          <IconGetRelative width={SizeDP(24)} height={SizeDP(24)} />
        </TouchableOpacity>
        <View style={{flex: 1}}>
          <TextInput
            ref={inputRef}
            style={{
              borderWidth: SizeDP(0.5),
              borderColor: '#00358026',
              borderRadius: SizeDP(8),
              padding: SizeDP(12),
            }}
            multiline
            placeholder={
              actionType === 'new'
                ? 'Add a comment...'
                : actionType === 'reply'
                ? 'Write a reply...'
                : 'Edit your comment...'
            }
            value={comment}
            onChangeText={handleChangeText}
          />
        </View>
        <TouchableOpacity onPress={handleReply} hitSlop={SizeDP(15)}>
          <IconSend width={SizeDP(24)} height={SizeDP(24)} />
        </TouchableOpacity>
      </View>
    </>
  );
};

export default CommunicationTicketScreen;
