import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {useNavigation, useRoute} from '@react-navigation/native';
import {IPostMessageEcontractTask} from '../review-flow/interface';
import {useAppSelector} from '../../../hooks/useRedux';
import {useEffect, useState} from 'react';
import {ScrollView, TouchableOpacity, View} from 'react-native';
import TextCM from '../../components/Text';
import styles from './styles';
import {IField} from './interface';
import {DateUtils} from '../../../utils';
import {FontSize, NavigationName} from '../../../constants';
import CheckboxCM from '../../components/Checkbox';
import {useReviewEcontractServices} from '../../../hooks/apiHooks/useReviewEcontractServices';

const ReviewEcontractFlow = () => {
  const router = useRoute<any>();
  const dataEcontractFlow = router?.params
    ?.dataUserTask as IPostMessageEcontractTask;
  const navigation = useNavigation<any>();
  const {accessToken} = useAppSelector(state => state.common);

  const [envelope, setEnvelope] = useState();
  const [headerFields, setHeaderFields] = useState<IField[]>([]);
  const [iframeUrl, setIframeUrl] = useState<string>('');
  const TicketReviewEcontractServices = useReviewEcontractServices();
  useEffect(() => {
    getEnvelopeById();
  }, []);

  const getEnvelopeById = async () => {
    try {
      const body = {
        id: '',
        refId: null,
        selector: 'flow_processing_kyta_erequest_view_contract',
        lookup: dataEcontractFlow.envelopeId,
        attrs: {},
        payload: JSON.stringify({
          envelopeId: dataEcontractFlow.envelopeId,
        }),
        body: null,
      };
      const data = await TicketReviewEcontractServices.getEnvelopeById(
        body,
        accessToken,
      );
      if (data && data.code === '0') {
        const envelopeData = JSON.parse(data?.result)?.envelope;
        const webviewInfo = JSON.parse(data?.result)?.webviewInfo;
        if (webviewInfo) {
          setIframeUrl(webviewInfo.iframeUrl);
        }
        if (envelopeData) {
          setEnvelope(envelopeData);
          const filterHeaderFields = envelopeData?.header?.fields?.filter(
            (field: any) => field.id !== 'envSubmittedFrom',
          );
          setHeaderFields(filterHeaderFields);
        }
      }
    } catch (error) {}
  };

  const getValue = (field: IField) => {
    if (field.id === 'envName') {
      return (
        <TouchableOpacity
          style={{flex: 1}}
          onPress={() =>
            navigation.navigate(NavigationName.ViewFileScreenEcontract, {
              iframeUrl: iframeUrl,
            })
          }>
          <>
            <TextCM
              style={[
                styles.txtValue,
                {
                  color: '#4583FF',
                  fontWeight: 'bold',
                  textDecorationLine: 'underline',
                  fontSize: FontSize.FontSmall,
                },
              ]}>
              {field?.value ?? '---'}
            </TextCM>
            <TextCM
              style={{
                fontSize: FontSize.FontTinier,
                fontStyle: 'italic',
              }}>
              Nhấn để xem chi tiết hợp đồng
            </TextCM>
          </>
        </TouchableOpacity>
      );
    }
    if (field.type === 'Date' || field.id === 'envDate') {
      return (
        <TextCM style={styles.txtValue}>
          {DateUtils.convertDateToDDMMYYYY_H_M(field.value) ?? '---'}
        </TextCM>
      );
    }
    if (field.type === 'Boolean') {
      return (
        <CheckboxCM
          onChange={() => {}}
          checked={field.value === 'true' ? true : false}
        />
      );
    }

    return <TextCM style={styles.txtValue}>{field.value ?? '---'}</TextCM>;
  };

  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM title="Thông tin chung hợp đồng" />
      <View style={styles.ctnMain}>
        <ScrollView style={styles.ctnBody}>
          {headerFields &&
            headerFields.map(field => (
              <View key={field.id} style={styles.ctnWrapperItem}>
                <TextCM style={styles.txtTitle}>{field.name}:</TextCM>
                <TextCM style={styles.txtValue}>{getValue(field)}</TextCM>
              </View>
            ))}
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>Trạng thái:</TextCM>
            <TextCM style={styles.txtValue}>{envelope?.status}</TextCM>
          </View>
        </ScrollView>
      </View>
    </SafeAreaView>
  );
};

export default ReviewEcontractFlow;
