import {
  FlatList,
  Image,
  Pressable,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';
import TextCM from '../../../../../components/Text';
import {IComments} from '../../interface';
import {IAccount} from '../../../../../../model/interface/account.interface';
import styles from './styles';
import FontSize, {SizeDP} from '../../../../../../constants/Size';
import {DateUtils} from '../../../../../../utils';
import {Font} from '../../../../../../constants';
import DropdownCM, {IDataDropdown} from '../../../../../components/DropDown';
import {IconEdit, IconTrash} from '../../../../../../assets/images';
import React, {RefObject, useEffect, useState} from 'react';
import {Icon, Input, MenuItem, OverflowMenu} from '@ui-kitten/components';
import {decodeUnicodeToEmoji} from '../../helper';

type Props = {
  data: IComments;
  user: IAccount;
  setComment: (text: string) => void;
  updateComment: (id: string, commentId: any) => void;
  deleteComment: (commentId: string) => void;
  allComments: Map<any, IComments[]>;
  onReplyClick: (action: 'reply' | 'edit', commentEdit: IComments) => void;
  level: number;
};

const actionComment = [
  {
    id: 1,
    key: 'Sửa bình luận',
    value: 'edit',
    icon: <IconEdit width={SizeDP(20)} height={SizeDP(20)} />,
  },
  {
    id: 2,
    key: 'Xóa bình luận',
    value: 'trash',
    icon: <IconTrash width={SizeDP(20)} height={SizeDP(20)} />,
  },
];

const CommentItem = React.memo(
  ({
    data,
    user,
    setComment,
    updateComment,
    deleteComment,
    allComments,
    onReplyClick,
    level,
  }: Props) => {
    const [menuVisible, setMenuVisible] = useState(false);
    const [showComment, setShowComment] = useState(false);
    // const [replyContent, setReplyContent] = useState('');
    const childComments = allComments.get(data.id) || [];
    const handleChangeAction = (item: IDataDropdown) => {
      switch (item.value) {
        case 'edit':
          toogleMenu();
          // updateComment(data.id);
          onReplyClick('edit', data);
          console.log('Edit');
          break;
        case 'trash':
          toogleMenu();
          deleteComment(data.id);
          console.log('Delete');
          break;
        default:
          break;
      }
    };

    const handleOnReply = () => {
      onReplyClick('reply', data);
      setShowComment(true);
    };

    const toogleShowComment = () => {
      setShowComment(!showComment);
    };

    const parseString = (str: string) => {
      return str.replace(/<\/?span[^>]*>/g, '');
    };

    const handleGetContent = (content: string) => {
      if (!content) {
        if (data.fileName) {
          return (
            <>
              <View style={styles.styleFile}>
                <TextCM numberOfLines={1} style={styles.content}>
                  {'' + data.fileName}
                </TextCM>
              </View>
            </>
          );
        } else {
          return (
            <>
              <TextCM style={styles.content}>'---'</TextCM>
            </>
          );
        }
      }
      if (!content.includes('class="user_tag"')) {
        if (!data.fileName) {
          return (
            <TextCM style={styles.content}>
              {decodeUnicodeToEmoji(content)}
            </TextCM>
          );
        } else {
          return (
            <>
              <TextCM style={styles.content}>
                {decodeUnicodeToEmoji(content)}
              </TextCM>
              <View style={styles.styleFile}>
                <TextCM numberOfLines={1} style={styles.content}>
                  {'' + data.fileName}
                </TextCM>
              </View>
            </>
          );
        }
      }
      if (content.includes('class="user_tag"')) {
        const parsedText = parseString(content);
        return (
          <View style={styles.bodyView}>
            <TextCM style={styles.tag}>{parsedText.split(' ')[0]}</TextCM>
            <TextCM style={styles.contentTag}>
              {decodeUnicodeToEmoji(parsedText.split(' ')[1])}
            </TextCM>
          </View>
        );
      }
    };

    const toogleMenu = () => {
      setMenuVisible(!menuVisible);
    };

    return (
      <>
        <View style={styles.ctnMain}>
          <View style={styles.ctnImage}>
            <Image
              width={SizeDP(24)}
              height={SizeDP(24)}
              borderRadius={SizeDP(12)}
              source={{
                uri: user?.imageUrl
                  ? user?.imageUrl
                  : 'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
          </View>
          <View style={{flex: 1}}>
            <View style={styles.ctnSecond}>
              <TextCM style={styles.ctnName}>
                {'' + user?.firstName + ' ' + user?.lastName + `• `}
              </TextCM>
              <TextCM style={styles.ctnEmail}>{user?.email}</TextCM>
            </View>
            <TextCM style={styles.ctnTime}>
              {DateUtils.convertDateToDDMMYYYY_H_M(data.lastModifiedDate)}
            </TextCM>
            {handleGetContent(data.content)}
            <View
              style={{
                flexDirection: 'row-reverse',
                alignItems: 'center',
                justifyContent: 'flex-end',
                columnGap: 10,
              }}>
              <Pressable onPress={handleOnReply} hitSlop={SizeDP(15)}>
                <TextCM style={styles.ctnReply}>Trả lời</TextCM>
              </Pressable>
              {childComments.length > 0 && (
                <Pressable
                  onPress={toogleShowComment}
                  style={{
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'flex-start',
                    columnGap: 6,
                    paddingVertical: 3,
                  }}>
                  <Icon name="corner-down-right" width={16} height={16} />
                  <TextCM
                    style={{
                      fontSize: 12,
                    }}>
                    Có {childComments?.length} bình luận
                  </TextCM>
                </Pressable>
              )}
            </View>
          </View>

          <View style={{}}>
            <OverflowMenu
              anchor={() => (
                <TouchableOpacity onPress={toogleMenu}>
                  <Icon width={20} height={20} name="more-vertical" />
                </TouchableOpacity>
              )}
              visible={menuVisible}
              onBackdropPress={toogleMenu}>
              {actionComment.map(action => (
                <MenuItem
                  key={action.value}
                  accessoryLeft={<Icon name={action.value} />}
                  title={action.key}
                  onPress={() => handleChangeAction(action)}
                />
              ))}
            </OverflowMenu>
          </View>
        </View>
        {showComment && (
          <View style={{marginLeft: 30, marginTop: 10}}>
            {childComments?.map(item => (
              <CommentItem
                key={item.id}
                data={item}
                user={user}
                setComment={setComment}
                deleteComment={deleteComment}
                updateComment={updateComment}
                allComments={allComments}
                onReplyClick={onReplyClick}
                level={level + 1}
              />
            ))}
          </View>
        )}
      </>
    );
  },
);

export default CommentItem;
