import {View} from 'react-native';
import {FC} from 'react';
import styles from './styles';
import {IndexPath, Select, SelectItem} from '@ui-kitten/components';
import TextCM from '../Text';
import {useTranslation} from 'react-i18next';

export interface IDropDown {
  label: string;
  value: any;
}

type Props = {
  placeholder?: string;
  size?: 'large' | 'medium' | 'small';
  onSelect?: any;
  valueSelect?: IndexPath[] | IndexPath;
  listData: IDropDown[];
  multiple?: boolean;
  isi18n?: boolean;
};

const DropdownListCM: FC<Props> = ({
  placeholder,
  onSelect,
  valueSelect,
  listData,
  multiple = false,
  size = 'large',
  isi18n = false,
}) => {
  const {t} = useTranslation();
  let displayData = '';
  if (valueSelect) {
    if (multiple) {
      displayData = valueSelect
        ?.map(index => {
          return listData?.[index.row].label.trim();
        })
        .join(', ');
    } else {
      displayData = listData?.[valueSelect?.row]?.label?.trim();
    }
  }

  return (
    <View style={styles.container}>
      <Select
        size={size}
        status="primary"
        multiSelect={multiple}
        appearance="default"
        placeholder={props => <TextCM {...props}>{placeholder}</TextCM>}
        selectedIndex={valueSelect}
        value={displayData}
        onSelect={(index: IndexPath[] | IndexPath) => onSelect(index)}>
        {listData &&
          listData.map(ser => (
            <SelectItem
              key={ser.value}
              title={props => <TextCM {...props}>{!isi18n ? ser.label : t(ser.label)}</TextCM>}
            />
          ))}
      </Select>
    </View>
  );
};

export default DropdownListCM;
